<template>
  <div style="margin-top: 10px">
    <el-card class="box-card" style="width: 400px">
      <div slot="header"><span>{{admin.name}}</span></div>
      <div>
        <div>电话号码：<el-tag>{{admin.telephone}}</el-tag></div>
        <div>手机号码：<el-tag>{{admin.phone}}</el-tag></div>
        <div>联系地址：<el-tag>{{admin.address}}</el-tag></div>
        <div>用户标签:<el-tag v-for="(r,index) in admin.roles" :key="index" type="success">{{r.nameZh}}</el-tag></div>
        <div style="display: flex; justify-content: space-around; margin-top: 10px">
          <el-button type="primary" @click="showEditPop">修改信息</el-button>
          <el-button type="danger" @click="showUpdatePwd">修改密码</el-button>
        </div>
      </div>
    </el-card>
    <el-dialog
      title="编辑用户信息"
      :visible.sync="dialogVisible"
      width="30%">
      <div>
        <table>
          <tr>
            <td>用户名:</td>
            <td><el-input v-model="adminForUpdate.name"></el-input></td>
          </tr>
          <tr>
            <td>电话号码:</td>
            <td><el-input v-model="adminForUpdate.telephone"></el-input></td>
          </tr>
          <tr>
            <td>手机号码:</td>
            <td><el-input v-model="adminForUpdate.phone"></el-input></td>
          </tr>
          <tr>
            <td>联系地址:</td>
            <td><el-input v-model="adminForUpdate.address"></el-input></td>
          </tr>
        </table>
      </div>
      <span slot="footer">
        <el-button @click="dialogVisible=!dialogVisible">cancel</el-button>
        <el-button type="primary" @click="updateUserInfo">confirm</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="修改密码"
      :visible.sync="pwdDialogVisible"
      width="30%">
      <div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100">
          <el-form-item label="请输入旧密码:" prop="oldPasswd">
            <el-input type="password" v-model="ruleForm.oldPasswd" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="请输入新密码:" prop="newPasswd">
            <el-input type="password" v-model="ruleForm.newPasswd"></el-input>
          </el-form-item>
          <el-form-item label="请确认新密码:" prop="checkPwd">
            <el-input type="password" v-model="ruleForm.checkPwd"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">submit</el-button>
            <el-button @click="resetForm('ruleForm')">reset</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'AdminInfo',
  data () {
    let validatePwd = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPwd !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    let validatePwd2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.newPasswd) {
        callback(new Error('两次输入密码不一致'))
      } else {
        callback()
      }
    }
    return {
      admin: {},
      dialogVisible: false,
      adminForUpdate: {},
      pwdDialogVisible: false,
      ruleForm: {
        newPasswd: '',
        oldPasswd: '',
        checkPwd: ''
      },
      rules: {
        pwd: [{ validator: validatePwd, trigger: 'blur' }],
        newPwd: [{ validator: validatePwd, trigger: 'blur' }],
        checkPwd: [{ validator: validatePwd2, trigger: 'blur' }]
      }
    }
  },
  mounted () {
    this.initUser()
  },
  methods: {
    initUser () {
      this.getRequest('/admin/info').then(resp => {
        if (resp) {
          this.admin = resp
          this.adminForUpdate = Object.assign({}, this.admin)
          window.sessionStorage.setItem('user', JSON.stringify(resp))
          this.$store.commit('INIT_ADMIN', resp)
        }
      })
    },
    showEditPop () {
      this.dialogVisible = !this.dialogVisible
    },
    updateUserInfo () {
      this.putRequest('/admin/info', this.adminForUpdate).then(resp => {
        if (resp) {
          this.initUser()
          this.dialogVisible = !this.dialogVisible
        }
      })
    },
    showUpdatePwd () {
      this.pwdDialogVisible = !this.pwdDialogVisible
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.putRequest('/admin/passwd', {
            oldPasswd: this.ruleForm.oldPasswd,
            newPasswd: this.ruleForm.newPasswd,
            adminId: this.admin.id
          }).then(resp => {
            if (resp) {
              // 更新密码成功后退出登录
              console.log(resp)
              this.postRequest('/logout')
              window.sessionStorage.removeItem('user')
              window.sessionStorage.removeItem('tokenStr')
              this.$store.commit('initRoutes', [])
              this.$router.replace('/')
            }
          })
        } else {
          console.log('error submit')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

