<template>
  <div>
    <el-form :rules="rules"
             v-loading = "loading"
             element-loading-text = "正在登录..."
             element-loading-spinner = "el-icon-loading"
             element-loading-background = "rgba(0,0,0,0.8)"
             ref="loginForm"
             :model="loginForm"
             class="loginContainer">
      <h3 class="loginTitle">Login</h3>
      <el-form-item prop="username">
        <el-input v-model="loginForm.username" placeholder="Please input username"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" auto-complete="false" v-model="loginForm.password" placeholder="Please input password"></el-input>
      </el-form-item>
      <el-form-item prop="code" class="el-form-item__content">
        <el-input auto-complete="false" v-model="loginForm.code" placeholder="Click to replace captcha" style="width: 250px;margin-right: 5px"></el-input>
        <img :src=captchaUrl alt="" @click="updateCaptcha">
      </el-form-item>
      <el-checkbox v-model="checked" class="loginRemember">Remember me</el-checkbox>
      <el-button type="primary" style="width: 100%" @click="submitLogin()">LOG IN</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      captchaUrl: '/kaptcha?time=' + new Date(),
      loginForm: {
        username: 'admin',
        password: '123',
        code: ''
      },
      loading: false,
      checked: true,
      rules: {
        username: [{ required: true, message: 'Please input username', trigger: 'blur' }],
        password: [{ required: true, message: 'Please input password', trigger: 'blur' }],
        code: [{ required: true, message: 'Please input captcha', trigger: 'blur' }]
      }
    }
  },
  methods: {
    updateCaptcha () {
      this.captchaUrl = '/kaptcha?time=' + new Date()
    },
    submitLogin () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.loading = true
          this.postRequest('/login', this.loginForm).then(resp => {
            if (resp) {
              this.loading = false
              const tokenStr = resp.object.token
              window.sessionStorage.setItem('tokenStr', tokenStr)
              // 处理页面跳转问题
              let path = this.$route.query.redirect
              this.$router.replace((path === '/' || path === undefined) ? '/home' : path)
            }
          })
        } else {
          this.$message.error('error submit!')
          return false
        }
      })
    }
  }
}
</script>

<style>
  .loginContainer {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 15px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .loginTitle {
    margin: 0 auto 40px auto;
    text-align: center;
  }

  .loginRemember {
    margin: 0 0 15px 0;
    text-align: left;
  }

  .el-form-item__content {
    display: flex;
    align-items: center;
  }
</style>
