<template>
  <div id="list">
    <ul style="padding-left: 0">
      <li v-for="item in sessions" :class="{ active: item.id === currentSessionId }" :key="item.id"
          v-on:click="changeCurrentSessionId(item.id)"><!--   :class="[item.id === currentSessionId ? 'active':'']" -->
        <img class="avatar" :src="item.user.img" :alt="item.user.name">
        <p class="name">{{ item.user.name }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'list',
  data () {
    return {}
  },
  computed: mapState([
    'sessions',
    'currentSessionId'
  ]),
  methods: {
    changeCurrentSessionId: function (id) {
      this.$store.commit('changeCurrentSessionId', id)
    }
  }
}
</script>

<style scoped>
#list  li {
  padding: 0 15px;
  border-bottom: 1px solid #292C33;
  cursor: pointer;
}

#list li li:hover {
  background-color: rgba(255, 255, 255, 0.03);
}

#list  li.active { /*注意这个是.不是冒号:*/
  background-color: rgba(255, 255, 255, 0.1);
}

#list .avatar {
  border-radius: 2px;
  width: 30px;
  height: 30px;
  vertical-align: middle;
}

#list .name {
  display: inline-block;
  margin-left: 15px;
}
</style>
