import axios from 'axios'
import { Message } from 'element-ui'

axios.interceptors.request.use(config => {
  if (window.sessionStorage.getItem('tokenStr')) {
    config.headers['user-token'] = window.sessionStorage.getItem('tokenStr')
  }
  return config
}, error => {
  console.error(error)
})

axios.interceptors.response.use(success => {
  if (success.status && success.status === 200) {
    if (success.data.code === 500 || success.data.code === 401 || success.data.code === 403) {
      Message.error({ message: success.data.message })
      return
    }
    if (success.data.message) {
      Message.success({ message: success.data.message })
    }
  }
  return success.data
}, error => {
  if (error.response.code === 504 || error.response.code === 404) {
    Message.error({ message: 'server was terminated' })
  } else if (error.message.code === 403) {
    Message.error({ message: 'access denied, please contact the administrator' })
  } else if (error.message.code === 401) {
    Message.error({ message: 'please login' })
    // router.replace('/')
  } else {
    if (error.response.data.message) {
      Message.error({ message: error.response.data.message })
    } else {
      Message.error({ message: 'unknown error!' })
    }
  }
})

const base = ''
export const postRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params
  })
}

export const putRequest = (url, params) => {
  return axios({
    method: 'put',
    url: `${base}${url}`,
    data: params
  })
}

export const getRequest = (url, params) => {
  return axios({
    method: 'get',
    url: `${base}${url}`,
    data: params
  })
}

export const deleteRequest = (url, params) => {
  return axios({
    method: 'delete',
    url: `${base}${url}`,
    data: params
  })
}
