<template>
  <div>
    <el-container>
      <el-header class="homeHeader">
        <div class="title">云易办</div>
        <div>
          <el-button
            type="text"
            icon="el-icon-bell"
            size="normal"
            style="margin-right: 8px; color: white;"
            @click="goChat"></el-button>
          <el-dropdown @command="commandHandler">
            <span class="el-dropdown-link"><i><img class="icon" src="../img/userFace.png" alt=""></i>{{ user.name }}</span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
              <el-dropdown-item command="setting">设置</el-dropdown-item>
              <el-dropdown-item command="logout">注销登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-container>
        <el-aside width="210px" class="aside">
          <el-menu router v-for="(item, index) in routes" :key="index" unique-opened>
            <el-submenu :index="index + ''" v-if="!item.hidden" class="list">
              <template slot="title">
                <i :class="item.iconCls" style="color: #40404a; margin-right: 8px"></i>
                <span style="font-size: 15px">{{ item.name }}</span>
              </template>
              <el-menu-item :index="children.path" v-for="(children, index2) in item.children" :key="index2" class="sublist">
                {{ children.name }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main class="content">
          <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$router.currentRoute.path !== '/home'">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item >{{this.$router.currentRoute.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <div class="homeWelcome" v-if="this.$router.currentRoute.path === '/home'">
            欢迎来到云易办系统
          </div>
          <router-view class="homeRouterView"/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      // user: JSON.parse(window.sessionStorage.getItem('user'))
    }
  },
  methods: {
    commandHandler (command) {
      if (command === 'logout') {
        this.$confirm('是否退出当前账号?', '提示', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.postRequest('/exit')
          // 清空用户信息
          window.sessionStorage.removeItem('tokenStr')
          window.sessionStorage.removeItem('user')
          //
          this.$store.commit('initRoutes', [])
          // 跳转到登录
          this.$router.replace('/')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消操作'
          })
        })
      }
      if (command === 'userInfo') {
        this.$router.push('/userInfo')
      }
    },
    goChat () {
      this.$router.push('/chat')
    }
  },
  computed: {
    routes () {
      return this.$store.state.routes
    },
    user () {
      return this.$store.state.currentAdmin
    }
  }
}
</script>

